






















import {Component, Vue} from "vue-property-decorator";
@Component({
    props: {
        value: String,
        handleInput: Function,
        handleBlur: Function,
        isValid: Boolean,
        isTouched: Boolean
    }
})
export default class LinkInput extends Vue {
    data(){
        return {
            validation: [
                (data: any) => {
                    if(typeof data === 'string') {
                        return 'Data should be number'
                    }
                    return true
                }
            ]
        }
    }
}
